<template>
  <div class="bg-gray-100 min-h-screen">
    <div class="container mx-auto px-4 py-8 max-w-6xl">
      <header class="text-center mb-10">
        <h1 class="text-[clamp(2rem,5vw,3rem)] font-bold text-dark mb-2">
          <i class="fa fa-smile-o text-primary mr-3"></i>表情识别系统
        </h1>
        <p class="text-gray-600 text-lg">实时检测人脸表情，支持图片上传和摄像头实时识别</p>
        <div 
          :class="statusClass" 
          class="mt-4 inline-block px-4 py-2 rounded-full"
        >
          <i :class="statusIcon" class="mr-2"></i>{{ statusText }}
        </div>
      </header>

      <div class="grid grid-cols-1 lg:grid-cols-2 gap-8">
        <!-- 左侧：输入区域 -->
        <div class="bg-white rounded-xl shadow-custom p-6">
          <h2 class="text-xl font-semibold mb-4 text-dark flex items-center">
            <i class="fa fa-input-text text-primary mr-2"></i>输入区域
          </h2>
          
          <div class="mb-6">
            <div class="flex gap-4 mb-4">
              <button 
                @click="handleUploadClick"
                class="flex-1 bg-primary hover:bg-primary/90 text-white py-3 px-4 rounded-lg transition-custom flex items-center justify-center"
                :disabled="!isServerConnected"
              >
                <i class="fa fa-upload mr-2"></i>上传图片
              </button>
              <button 
                @click="startCamera"
                class="flex-1 bg-secondary hover:bg-secondary/90 text-white py-3 px-4 rounded-lg transition-custom flex items-center justify-center"
                :disabled="!isServerConnected || isCameraActive"
              >
                <i class="fa fa-video-camera mr-2"></i>开启摄像头
              </button>
              <button 
                @click="stopCamera"
                class="flex-1 bg-danger hover:bg-danger/90 text-white py-3 px-4 rounded-lg transition-custom flex items-center justify-center"
                :class="{ hidden: !isCameraActive }"
              >
                <i class="fa fa-stop mr-2"></i>关闭摄像头
              </button>
            </div>
            
            <input 
              type="file" 
              ref="imageUpload" 
              accept="image/*" 
              class="hidden"
              @change="handleImageUpload"
            >
            
            <div class="relative bg-gray-100 rounded-lg overflow-hidden border-2 border-dashed border-gray-300">
              <div id="preview-container" class="aspect-video flex items-center justify-center">
                <div 
                  id="placeholder" 
                  class="text-center text-gray-500"
                  :class="{ hidden: showPreview || isCameraActive }"
                >
                  <i class="fa fa-image text-5xl mb-3"></i>
                  <p>上传图片或开启摄像头查看预览</p>
                </div>
                <img 
                  id="image-preview" 
                  class="w-full h-full object-contain"
                  :src="imagePreviewUrl"
                  :class="{ hidden: !showPreview || isCameraActive }"
                  alt="预览图"
                >
                <video 
                  id="camera-preview" 
                  class="w-full h-full object-contain"
                  autoplay 
                  playsinline
                  :class="{ hidden: !isCameraActive }"
                  ref="cameraPreview"
                ></video>
              </div>
              <div 
                id="loading-overlay" 
                class="absolute inset-0 bg-black/50 flex items-center justify-center"
                :class="{ hidden: !isLoading }"
              >
                <div class="bg-white p-4 rounded-lg">
                  <i class="fa fa-circle-o-notch fa-spin text-primary text-2xl"></i>
                  <p class="mt-2 text-dark">识别中...</p>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <!-- 右侧：结果区域 -->
        <div class="bg-white rounded-xl shadow-custom p-6">
          <h2 class="text-xl font-semibold mb-4 text-dark flex items-center">
            <i class="fa fa-bar-chart text-primary mr-2"></i>识别结果
          </h2>
          
          <div id="results-container" class="bg-gray-50 rounded-lg p-4 min-h-[300px]">
            <div 
              id="no-results" 
              class="text-center text-gray-500 py-12"
              :class="{ hidden: showResults }"
            >
              <i class="fa fa-info-circle text-4xl mb-3" :class="{ 'text-orange-500': errorMessage }"></i>
              <p>{{ errorMessage || '识别结果将显示在这里' }}</p>
            </div>
            
            <div id="results" :class="{ hidden: !showResults }">
              <div id="emotion-result" class="text-center mb-6">
                <div id="emotion-icon" class="text-6xl mb-2">{{ emotionIcon }}</div>
                <h3 id="emotion-name" class="text-2xl font-bold text-dark">{{ emotionName }}</h3>
                <p id="emotion-confidence" class="text-gray-600">{{ emotionConfidence }}</p>
              </div>
              
              <div class="border-t pt-4">
                <h4 class="font-semibold text-dark mb-2">详细信息</h4>
                <div id="face-details" class="text-gray-700">
                  <p><strong>人脸位置:</strong> X: {{ faceX }}, Y: {{ faceY }}, 宽度: {{ faceW }}, 高度: {{ faceH }}</p>
                </div>
              </div>
            </div>
          </div>
          
          <div class="mt-6">
            <h3 class="font-semibold text-dark mb-3">表情概率分布</h3>
            <div id="emotion-chart" class="space-y-2">
              <div 
                class="flex items-center gap-3" 
                v-for="emotion in emotions" 
                :key="emotion"
              >
                <div class="w-24 flex items-center">
                  <span class="mr-2">{{ emotionIcons[emotion] }}</span>
                  <span>{{ emotion }}</span>
                </div>
                <div class="flex-1 h-4 bg-gray-200 rounded-full overflow-hidden">
                  <div 
                    :class="['h-full rounded-full transition-all duration-500 ease-out', emotion === dominantEmotion ? 'bg-primary' : 'bg-gray-300']"
                    :style="{ width: emotionProbabilities[emotion] + '%' }"
                  ></div>
                </div>
                <div class="w-12 text-right">{{ emotionProbabilities[emotion].toFixed(1) }}%</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <footer class="mt-12 text-center text-gray-500 text-sm">
        <p>表情识别系统 &copy; 2023 | 使用深度学习技术实时分析面部表情</p>
      </footer>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, onBeforeUnmount, computed } from 'vue';

// 后端API地址
const API_URL = 'https://123.56.203.202';

// 响应式状态
const isServerConnected = ref(false);
const statusText = ref('正在连接服务器...');
const statusIcon = ref('fa fa-circle-o-notch fa-spin');
const isCameraActive = ref(false);
const videoStream = ref(null);
const detectionInterval = ref(null);
const imagePreviewUrl = ref('');
const showPreview = ref(false);
const isLoading = ref(false);
const showResults = ref(false);
const errorMessage = ref('');

// 识别结果相关状态
const emotionName = ref('');
const emotionConfidence = ref('');
const emotionIcon = ref('');
const faceX = ref(0);
const faceY = ref(0);
const faceW = ref(0);
const faceH = ref(0);
const dominantEmotion = ref('');
const emotionProbabilities = reactive({
  'Angry': 0,
  'Disgust': 0,
  'Fear': 0,
  'Happy': 0,
  'Neutral': 0,
  'Sad': 0,
  'Surprise': 0
});

// DOM引用
const imageUpload = ref(null);
const cameraPreview = ref(null);

// 表情对应的图标
const emotionIcons = {
  'Angry': '😠',
  'Disgust': '🤢',
  'Fear': '😨',
  'Happy': '😊',
  'Neutral': '😐',
  'Sad': '😢',
  'Surprise': '😲'
};

const emotions = Object.keys(emotionIcons);

// 状态样式计算属性
const statusClass = computed(() => {
  if (isServerConnected.value) {
    return 'bg-green-100 text-green-800';
  } else {
    return 'bg-red-100 text-red-800';
  }
});

// 初始化
onMounted(() => {
  checkServerStatus();
  const serverCheckInterval = setInterval(checkServerStatus, 5000);
  
  // 清理函数
  onBeforeUnmount(() => {
    clearInterval(serverCheckInterval);
    stopCamera();
  });
});

// 检查服务器状态
const checkServerStatus = async () => {
  try {
    const response = await fetch(`${API_URL}/health`);
    const data = await response.json();
    
    if (response.ok && data.status === 'healthy') {
      isServerConnected.value = true;
      statusText.value = '服务器已连接';
      statusIcon.value = 'fa fa-check';
      return true;
    } else {
      throw new Error('服务器状态异常');
    }
  } catch (error) {
    isServerConnected.value = false;
    statusText.value = '无法连接服务器';
    statusIcon.value = 'fa fa-times';
    return false;
  }
};

// 处理图片上传点击
const handleUploadClick = () => {
  imageUpload.value.click();
};

// 处理图片上传
const handleImageUpload = (event) => {
  const file = event.target.files[0];
  if (!file) return;
  
  // 显示预览
  const reader = new FileReader();
  reader.onload = (e) => {
    imagePreviewUrl.value = e.target.result;
    showPreview.value = true;
    uploadImageForRecognition(e.target.result);
  };
  reader.readAsDataURL(file);
  
  // 重置input值，允许重复上传同一文件
  event.target.value = '';
};

// 上传图片进行识别
const uploadImageForRecognition = async (imageDataUrl) => {
  if (!await checkServerStatus()) return;
  
  isLoading.value = true;
  errorMessage.value = '';
  
  try {
    const response = await fetch(`${API_URL}/predict/image`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ base64: imageDataUrl })
    });
    
    const result = await response.json();
    
    if (result.status === 'success' && result.predictions.length > 0) {
      displayResults(result.predictions);
    } else {
      showError('未检测到人脸或表情');
    }
  } catch (error) {
    showError(`识别失败: ${error.message}`);
  } finally {
    isLoading.value = false;
  }
};

// 启动摄像头
const startCamera = async () => {
  if (!await checkServerStatus()) return;
  
  try {
    videoStream.value = await navigator.mediaDevices.getUserMedia({ 
      video: { width: { ideal: 1280 }, height: { ideal: 720 } } 
    });
    
    cameraPreview.value.srcObject = videoStream.value;
    isCameraActive.value = true;
    showPreview.value = false;
    
    // 开始实时识别
    startRealTimeDetection();
  } catch (error) {
    showError(`无法访问摄像头: ${error.message}`);
    console.error('摄像头访问错误:', error);
  }
};

// 停止摄像头
const stopCamera = () => {
  if (videoStream.value) {
    videoStream.value.getTracks().forEach(track => track.stop());
    videoStream.value = null;
  }
  
  if (detectionInterval.value) {
    clearInterval(detectionInterval.value);
    detectionInterval.value = null;
  }
  
  isCameraActive.value = false;
  showResults.value = false;
};

// 开始实时识别
const startRealTimeDetection = () => {
  if (detectionInterval.value) {
    clearInterval(detectionInterval.value);
  }
  
  // 每2000ms识别一次 (可根据需要调整)
  detectionInterval.value = setInterval(async () => {
    if (!isCameraActive.value) return;
    
    try {
      // 捕获当前视频帧
      const canvas = document.createElement('canvas');
      canvas.width = cameraPreview.value.videoWidth;
      canvas.height = cameraPreview.value.videoHeight;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(cameraPreview.value, 0, 0, canvas.width, canvas.height);
      
      // 转为Base64
      const base64 = canvas.toDataURL('image/jpeg');
      
      // 发送到后端识别
      const response = await fetch(`${API_URL}/predict/video/frame`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ base64: base64 })
      });
      
      const result = await response.json();
      
      if (result.status === 'success' && result.predictions.length > 0) {
        displayResults(result.predictions);
      } else {
        showError(result.error || '未检测到人脸');
      }
    } catch (error) {
      console.error('实时识别错误:', error);
    }
  }, 2000);
};

// 显示识别结果
const displayResults = (predictions) => {
  // 取第一个检测到的人脸
  const firstFace = predictions[0];
  if (!firstFace) {
    showError('未检测到人脸');
    return;
  }
  
  // 更新主要表情结果
  emotionName.value = firstFace.emotion;
  emotionIcon.value = emotionIcons[firstFace.emotion] || '😐';
  emotionConfidence.value = `置信度: ${(firstFace.confidence * 100).toFixed(1)}%`;
  
  // 更新人脸位置信息
  const [x, y, w, h] = firstFace.bounding_box;
  faceX.value = x;
  faceY.value = y;
  faceW.value = w;
  faceH.value = h;
  
  // 更新表情概率图表
  dominantEmotion.value = firstFace.emotion;
  updateEmotionChart(firstFace.emotion);
  
  // 显示结果
  showResults.value = true;
  errorMessage.value = '';
};

// 更新表情概率图表
const updateEmotionChart = (dominantEmotion) => {
  emotions.forEach(emotion => {
    // 为演示创建模拟概率
    let probability = 0.1 + Math.random() * 0.3;
    if (emotion === dominantEmotion) {
      probability = 0.7 + Math.random() * 0.3;
    }
    probability = Math.min(probability, 1);
    
    emotionProbabilities[emotion] = probability * 100;
  });
};

// 显示错误信息
const showError = (message) => {
  errorMessage.value = message;
  showResults.value = false;
};

// 页面关闭时清理资源
onBeforeUnmount(() => {
  stopCamera();
});
</script>

<style scoped>
@layer utilities {
  .content-auto {
    content-visibility: auto;
  }
  .shadow-custom {
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  .transition-custom {
    transition: all 0.3s ease;
  }
}

.hidden {
  display: none !important;
}
</style>
