<template>
    <!-- 顶部栏 -->
    <div id="top_bar">
        <van-nav-bar :title="title_mess" :fixed="true" :placeholder="true" @click-left="this.$router.push('/side')">
            <template #left>
                &nbsp;
                <van-image
                    round
                    width="32px"
                    height="32px"
                    :src="require('../assets/images/my_profile.jpg')"/>
                &nbsp;
                <!-- 显示用户名 -->
                <span class="username">{{ username }}</span>
            </template>
            <template #right>
                <van-popover v-model:show="showPopover" :actions="actions" placement="bottom-end" :offset="[13, 8]">
                    <template #reference>
                        &nbsp;
                        <van-icon name="plus" color="white" size="15"/>
                        &nbsp;
                    </template>
                </van-popover>
            </template>
        </van-nav-bar>
    </div>
</template>

<script>
export default {
    props: ['title'],
    data() {
        return {
            title_mess: this.title,
            showPopover: false, // 是否展示气泡弹出框
            actions: [
                { text: '这是彩蛋', icon: 'gift-o' },
            ],
            username: '' // 存储用户名
        };
    },
    watch: {
        // 监听父组件传来的title数据，实时更新顶部栏标题
        title() {
            switch(this.title) {
                case 'message': this.title_mess = '消息'; break;
                case 'friends': this.title_mess = '联系人'; break;
                case 'discover': this.title_mess = '动态'; break;
            }
        }
    },
    mounted() {
        // 初始化标题
        if(this.$route.path == '/home/message') {
            this.title_mess = '消息';
        }
        else if(this.$route.path == '/home/friends') {
            this.title_mess = '联系人';
        }
        else if(this.$route.path == '/home/discover') {
            this.title_mess = '动态';
        }

        // 从localStorage获取用户名
        const userInfo = localStorage.getItem('currentUser');
        if (userInfo) {
            try {
                const parsedUser = JSON.parse(userInfo);
                this.username = parsedUser.username || '';
            } catch (error) {
                console.error('解析用户信息失败', error);
                this.username = '';
            }
        }
    }
}
</script>

<style>
#top_bar {
    --van-nav-bar-background-color: #12B8F6;
    --van-nav-bar-title-text-color: white;
}
.van-nav-bar__content {
    height: 50px;
}

/* 用户名样式 */
.username {
    color: white;
    font-size: 16px;
    font-weight: 500;
    vertical-align: middle;
    /* 确保在小屏幕上不会换行 */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 120px;
}
</style>
    