<template>
  <div id="dialog">
    <!-- 顶部导航栏 -->
    <van-nav-bar 
      :fixed="true" 
      :placeholder="true" 
      left-arrow 
      @click-left="backToHomeMessage()"
      custom-style="z-index: 999;"
    >
      <template #right>
        <van-icon name="wap-nav" size="20" />
      </template>
      <template #title>
        <div style="line-height: 20px;">
          {{ name }}
          <br>
          <van-image
            width="90"
            height="10"
            :src="require('@/assets/images/手机在线.png')" 
            style="margin-top: 2px;"
          />
        </div>
      </template>
    </van-nav-bar>
    
    <!-- 聊天会话内容区域 -->
    <div id="dialog_content" class="chat-container">
      <div class="messages" ref="messagesContainer">
        <!-- 初始欢迎消息 -->
        <div class="message-item ai-message">
          <div class="avatar">
            <van-icon name="robot" size="24" />
          </div>
          <div class="message-bubble">
            你好！我是{{ name }}，有什么可以帮你的吗？
          </div>
        </div>
        
        <!-- 动态消息列表 -->
        <div 
          v-for="(msg, index) in messages" 
          :key="index" 
          :class="['message-item', msg.sender === 'user' ? 'user-message' : 'ai-message']"
        >
          <div class="avatar">
            <van-icon 
              :name="msg.sender === 'user' ? 'user' : 'robot'" 
              size="24" 
            />
          </div>
          
          <!-- 文本消息 -->
          <div 
            v-if="msg.type === 'text'"
            class="message-bubble" 
            :class="msg.isError ? 'error-message' : ''"
          >
            {{ msg.content }}
          </div>
          
          <!-- 语音消息 -->
          <div v-if="msg.type === 'voice'" class="voice-bubble">
            <div class="voice-controls">
              <van-icon 
                :name="msg.isPlaying ? 'pause-circle' : 'play-circle'" 
                size="20" 
                @click="toggleVoicePlayback(index)"
                class="play-icon"
              />
              <div class="voice-wave">
                <div :style="{ height: '100%' }"></div>
                <div :style="{ height: '60%' }"></div>
                <div :style="{ height: '80%' }"></div>
                <div :style="{ height: '40%' }"></div>
              </div>
              <span class="voice-duration">{{ formatDuration(msg.duration) }}</span>
            </div>
            
            <!-- 语音操作按钮 -->
            <div class="voice-actions" v-if="msg.sender === 'user'">
              <van-button 
                size="mini" 
                type="primary" 
                @click="convertVoiceToText(index)"
                round
              >
                转文字
              </van-button>
              <van-button 
                size="mini" 
                type="info" 
                @click="analyzeVoiceTone(index)"
                round
              >
                分析语调
              </van-button>
              <van-button 
                size="mini" 
                type="warning" 
                @click="calculateSpeechSpeed(index)"
                round
                :disabled="!msg.textResult"
              >
                语速
              </van-button>
              <van-button 
                size="mini" 
                type="success" 
                @click="analyzeLanguageLogicLogic(index)"
                round
                :disabled="!msg.textResult"
              >
                语言逻辑
              </van-button>
            </div>
            
            <!-- 语音转文字结果 -->
            <div v-if="msg.textResult" class="voice-text-result">
              {{ msg.textResult }}
            </div>
            
            <!-- 语速速计算结果 -->
            <div v-if="msg.speechSpeed" class="voice-speed-result">
              语速: {{ msg.speechSpeed }} 字/秒 (约 {{ Math.round(msg.speechSpeed * 60) }} 字/分钟)
            </div>
            
            <!-- 语调分析结果 -->
            <div v-if="msg.toneResult" class="voice-tone-result">
              <div>语调: {{ msg.toneResult.tone_type }}</div>
              <div>基频均值: {{ msg.toneResult.f0_mean }}Hz</div>
              <div>基频范围: {{ msg.toneResult.f0_min }}-{{ msg.toneResult.f0_max }}Hz</div>
            </div>
            
            <!-- 语言逻辑逻辑分析结果 -->
            <div v-if="msg.logicResult" class="voice-logic-result">
              <div><strong>语言逻辑分析:</strong></div>
              <div>{{ msg.logicResult.analysis }}</div>
              <div v-if="msg.logicResult.score"><strong>连贯性评分:</strong> {{ msg.logicResult.score }}/10</div>
            </div>
          </div>
          
          <!-- 表情和姿态分析消息 -->
          <div v-if="msg.type === 'expression'" class="expression-bubble">
            <div class="expression-image-container">
              <img :src="msg.imageUrl" class="expression-image" alt="表情和姿态分析截图">
            </div>
            
            <!-- 表情分析结果 -->
            <div v-if="msg.expressionResults && msg.expressionResults.length" class="expression-results">
              <div><strong>表情分析:</strong></div>
              <div v-for="(result, i) in msg.expressionResults" :key="i">
                {{ result.emotion }} (可信度: {{ (result.confidence * 100).toFixed(1) }}%)
              </div>
            </div>
            
            <!-- 姿态分析结果 -->
            <div v-if="msg.poseResult" class="pose-result">
              <div><strong>姿态分析:</strong></div>
              <div>动作: {{ msg.poseResult.pose }}</div>
              <div>置信度: {{ (msg.poseResult.score * 100).toFixed(1) }}%</div>
              <div>帧率: {{ msg.poseResult.fps }}fps</div>
            </div>
            
            <!-- 综合分析结果 -->
            <div v-if="msg.comprehensiveAnalysis" class="comprehensive-analysis">
              <div><strong>综合分析:</strong></div>
              <div>{{ msg.comprehensiveAnalysis }}</div>
            </div>
            
            <!-- 操作按钮 -->
            <div class="expression-actions" v-if="msg.sender === 'user'">
              <van-button 
                size="mini" 
                type="primary" 
                @click="analyzeComprehensive(index)"
                round
              >
                综合分析
              </van-button>
            </div>
          </div>
        </div>
        
        <!-- 正在输入指示器 -->
        <div 
          v-if="isTyping" 
          :id="typingId"
          class="message-item ai-message"
        >
          <div class="avatar">
            <van-icon name="robot" size="24" />
          </div>
          <div class="message-bubble typing">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 底部输入栏 -->
    <div id="dialog_bottombar">
      <div id="dialog_bottombar_inside">
        <van-field 
          type="textarea" 
          rows="1" 
          :autosize="{maxHeight:100, minHeight:40}" 
          v-model="message" 
          style="border-radius:25px; padding:10px;" 
          @keypress.enter.prevent="sendMessage"
          placeholder="输入消息..."
        />
        <van-row style="padding-top: 8px;">
          <van-col span="4" style="text-align:center;">
            <van-icon 
              :name="isRecording ? 'stop' : require('@/assets/icon/icon_dialog_语音.png')" 
              size="20" 
              @click="toggleRecording"
              :class="isRecording ? 'recording' : ''"
            />
          </van-col>
          <van-col span="4" style="text-align:center;">
            <van-icon name="image" size="20" />
          </van-col>
          <van-col span="4" style="text-align:center;">
            <van-icon 
              :name="require('@/assets/icon/icon_dialog_相机.png')" 
              size="20" 
              @click="toggleCamera"
              :class="isCameraActive ? 'camera-active' : ''"
            />
          </van-col>
          <van-col span="4" style="text-align:center;">
            <van-icon name="red-envelope" size="20" />
          </van-col>
          <van-col span="4" style="text-align:center;">
            <van-icon name="smile-o" size="20" />
          </van-col>
          <van-col span="4" style="text-align:center;">
            <van-button 
              round 
              type="primary" 
              size="small"
              @click="sendMessage"
              style="width:35px; height:35px; padding:0; display:flex; align-items:center; justify-content:center;"
              :disabled="!message.trim()"
            >
              发送
              <van-icon name="paper-plane-o" size="16" style="margin-left: 2px;" />
            </van-button>
          </van-col>
        </van-row>
      </div>
    </div>
    
    <!-- 相机模态框 -->
    <van-popup
      v-model:show="isCameraModalVisible"
      position="bottom"
      :style="{ height: '80%' }"
    >
      <div class="camera-modal">
        <div class="camera-header">
          <h3>表情与姿态识别</h3>
          <van-icon name="close" @click="toggleCamera" />
        </div>
        
        <div class="camera-preview">
          <video 
            ref="videoElement" 
            autoplay 
            playsinline
            class="camera-video"
          ></video>
          
          <!-- 加载中状态 -->
          <div v-if="isLoadingCamera" class="camera-loading">
            <van-loading type="spinner" color="#1677ff" />
            <p>正在初始化相机...</p>
          </div>
          
          <!-- 错误提示 -->
          <div v-if="cameraError" class="camera-error">
            <van-icon name="warning-circle" color="#f53f3f" size="24" />
            <p>{{ cameraError }}</p>
          </div>
        </div>
        
        <div class="camera-controls">
          <van-button 
            type="primary" 
            round 
            @click="captureImage"
            :disabled="!isCameraActive || isProcessingImage"
          >
            <van-icon name="camera" size="18" style="margin-right: 5px;" />
            {{ isProcessingImage ? '处理中...' : '拍照分析' }}
          </van-button>
          
          <van-button 
            type="info" 
            round 
            @click="startContinuousAnalysis"
            :disabled="!isCameraActive || isAnalyzingContinuously"
          >
            <van-icon name="play" size="18" style="margin-right: 5px;" />
            {{ isAnalyzingContinuously ? '停止连续分析' : '连续分析' }}
          </van-button>
        </div>
      </div>
    </van-popup>
    
    <!-- 录音提示 -->
    <div v-if="isRecording" class="recording-indicator">
      <div class="recording-dot"></div>
      <p>正在录音...{{ recordingSeconds }}s</p>
      <p>松开结束录音</p>
    </div>
    
    <!-- 服务状态提示 -->
    <div 
      class="status-bar" 
      :class="statusClass"
      style="z-index: 998;"
    >
      {{ statusText }}
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: this.$route.params.name,
      message: "",
      messages: [],
      isTyping: false,
      typingId: "",
      API_BASE_URL: 'https://123.56.203.202',
      statusText: '连接中...',
      statusClass: 'text-xs text-gray-500 mt-2 text-center',
      commonHeaders: {
        'ngrok-skip-browser-warning': '1'
      },
      
      // 语音相关状态
      isRecording: false,
      mediaRecorder: null,
      audioBlob: null,
      recordingSeconds: 0,
      recordingTimer: null,
      audioContext: null,
      audioElements: {},
      mediaStream: null,
      audioChunks: [],

      // 相机相关状态
      isCameraModalVisible: false,
      isCameraActive: false,
      isLoadingCamera: false,
      cameraError: null,
      isProcessingImage: false,
      isAnalyzingContinuously: false,
      continuousAnalysisTimer: null,
      detectionInterval: null,
      videoStream: null
    };
  },
  mounted() {
    this.testConnection();
    this.initContainerHeight();
    window.addEventListener('resize', this.initContainerHeight);
    this.checkRecordingSupport();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.initContainerHeight);
    this.stopRecording();
    Object.values(this.audioElements).forEach(audio => {
      if (audio) {
        audio.pause();
      }
    });
    
    // 清理相机相关资源
    this.stopCamera();
    if (this.continuousAnalysisTimer) {
      clearInterval(this.continuousAnalysisTimer);
    }
    if (this.detectionInterval) {
      clearInterval(this.detectionInterval);
    }
  },
  methods: {
    // 返回主页面消息界面
    backToHomeMessage() {
      this.$router.push('/home/message');
    },
    
    // 发送文本消息
    async sendMessage() {
      const prompt = this.message.trim();
      if (!prompt) return;
      
      this.addMessage('user', prompt, 'text');
      this.message = '';
      this.scrollToBottom();
      
      const typingId = this.addTypingIndicator();
      
      try {
        fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        }).then(response => {
          this.removeTypingIndicator(typingId);
          
          if (!response.ok) {
            throw new Error(`请求失败: ${response.status}`);
          }
          
          return response.json();
        }).then(data => {
          if (data.status === 'success') {
            this.addMessage('ai', data.result, 'text');
          } else {
            this.addMessage('ai', `错误: ${data.result}`, 'text', true);
          }
          this.scrollToBottom();
        }).catch(error => {
          this.removeTypingIndicator(typingId);
          this.addMessage('ai', `请求失败: ${error.message}`, 'text', true);
          this.scrollToBottom();
          console.error('发送消息失败:', error);
        });
      } catch (error) {
        this.removeTypingIndicator(typingId);
        this.addMessage('ai', `请求失败: ${error.message}`, 'text', true);
        this.scrollToBottom();
        console.error('发送消息失败:', error);
      }
    },
    
    // 检查浏览器是否支持录音
    checkRecordingSupport() {
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        console.log('浏览器支持录音功能');
        navigator.mediaDevices.getUserMedia({ audio: true })
          .then(() => {
            console.log('录音权限已授予');
          })
          .catch(error => {
            console.warn('录音权限未授予:', error);
            this.showStatus('请在使用录音功能前授予麦克风权限', 'info');
          });
      } else {
        this.showStatus('您的浏览器不支持录音功能', 'error');
      }
    },
    
    // 切换录音状态
    async toggleRecording() {
      if (this.isRecording) {
        this.stopRecording();
      } else {
        try {
          if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
            throw new Error('您的浏览器不支持录音功能，请使用最新浏览器');
          }
          
          if (window.location.protocol !== 'https:' && window.location.hostname !== 'localhost') {
            this.showStatus('录音功能需要在HTTPS环境下运行', 'error');
            return;
          }
          
          this.mediaStream = await navigator.mediaDevices.getUserMedia({ 
            audio: {
              sampleRate: 16000,
              channelCount: 1,
              echoCancellation: true,
              noiseSuppression: true,
              autoGainControl: true
            } 
          });
          
          // 提供多种格式选项，并找到浏览器支持的格式
          const possibleMimeTypes = [
            'audio/wav;codecs=PCM',
            'audio/webm',
            'audio/mp4',
            'audio/mpeg'
          ];
          
          // 找到浏览器支持的第一个格式
          let mimeType = possibleMimeTypes.find(type => MediaRecorder.isTypeSupported(type));
          
          // 如果没有找到支持的格式，不指定格式让浏览器使用默认
          const options = mimeType ? { mimeType } : {};
          
          if (!mimeType) {
            console.warn('浏览器不支持任何推荐的音频格式，将使用默认格式');
            this.showStatus('注意：浏览器不支持标准音频格式，可能影响识别效果', 'warning');
          } else if (mimeType !== 'audio/wav;codecs=PCM') {
            console.warn(`浏览器不支持audio/wav;codecs=PCM，使用${mimeType}格式`);
            this.showStatus(`注意：使用${mimeType}格式，可能影响识别效果`, 'info');
          }
          
          this.mediaRecorder = new MediaRecorder(this.mediaStream, options);
          this.audioChunks = [];
          
          this.mediaRecorder.addEventListener('dataavailable', (event) => {
            this.audioChunks.push(event.data);
          });
          
          this.mediaRecorder.addEventListener('stop', async () => {
            // 根据使用的MIME类型创建Blob
            const audioBlob = new Blob(this.audioChunks, { 
              type: mimeType || 'audio/wav' 
            });
            this.audioBlob = await this.convertToValidWav(audioBlob);
            
            if (this.mediaStream) {
              this.mediaStream.getTracks().forEach(track => track.stop());
              this.mediaStream = null;
            }
            
            if (this.audioBlob) {
              this.addVoiceMessage();
            }
          });
          
          this.mediaRecorder.start();
          this.isRecording = true;
          this.recordingSeconds = 0;
          
          this.recordingTimer = setInterval(() => {
            this.recordingSeconds++;
            if (this.recordingSeconds >= 60) {
              this.stopRecording();
            }
          }, 1000);
          
          this.showStatus('正在录音...', 'info');
          
        } catch (error) {
          console.error('录音失败:', error);
          this.isRecording = false;
          
          // 更详细的错误处理
          if (error.name === 'NotAllowedError') {
            this.showStatus('录音权限被拒绝，请在浏览器设置中允许麦克风访问', 'error');
          } else if (error.name === 'NotFoundError') {
            this.showStatus('未找到麦克风设备', 'error');
          } else if (error.name === 'NotReadableError') {
            this.showStatus('麦克风被占用或不可用', 'error');
          } else if (error.name === 'NotSupportedError') {
            this.showStatus('您的浏览器不支持录音功能', 'error');
          } else {
            this.showStatus(`录音失败: ${error.message}`, 'error');
          }
        }
      }
    },
    
    // 停止录音
    stopRecording() {
      if (this.isRecording && this.mediaRecorder) {
        this.mediaRecorder.stop();
        this.isRecording = false;
        
        if (this.recordingTimer) {
          clearInterval(this.recordingTimer);
          this.recordingTimer = null;
        }
        
        if (this.mediaStream) {
          this.mediaStream.getTracks().forEach(track => track.stop());
          this.mediaStream = null;
        }
        
        this.showStatus('录音已完成', 'success');
      }
    },
    
    // 添加语音消息到列表
    addVoiceMessage() {
      if (!this.audioBlob) return;
      
      const audioUrl = URL.createObjectURL(this.audioBlob);
      const duration = this.recordingSeconds;
      
      const audio = new Audio(audioUrl);
      
      this.messages.push({
        sender: 'user',
        type: 'voice',
        blob: this.audioBlob,
        url: audioUrl,
        duration: duration,
        isPlaying: false,
        textResult: null,
        toneResult: null,
        speechSpeed: null,
        logicResult: null
      });
      
      this.audioElements[this.messages.length - 1] = audio;
      
      this.scrollToBottom();
    },
    
    // 播放或暂停语音
    toggleVoicePlayback(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'voice') return;
      
      const audio = this.audioElements[index];
      if (!audio) return;
      
      if (msg.isPlaying) {
        audio.pause();
        msg.isPlaying = false;
      } else {
        this.messages.forEach((m, i) => {
          if (m.isPlaying && i !== index) {
            m.isPlaying = false;
            const otherAudio = this.audioElements[i];
            if (otherAudio) otherAudio.pause();
          }
        });
        
        audio.play()
          .then(() => {
            msg.isPlaying = true;
            audio.onended = () => {
              msg.isPlaying = false;
            };
          })
          .catch(error => {
            console.error('播放失败:', error);
            this.showStatus('播放失败，请重试', 'error');
          });
      }
    },
    
    // 语音转文字
    async convertVoiceToText(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'voice' || !msg.blob) return;
      
      this.showStatus('正在转换语音为文字...', 'info');
      
      try {
        const formData = new FormData();
        formData.append('audio', msg.blob, 'recording.wav');
        
        const response = await fetch(`${this.API_BASE_URL}/recognize_speech`, {
          method: 'POST',
          headers: this.commonHeaders,
          body: formData
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          console.error('语音转文字API错误:', errorText);
          throw new Error(`服务器错误: ${response.status} - ${errorText.substring(0, 100)}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success') {
          // 使用扩展运算符创建新对象，触发响应式更新
          this.messages[index] = {
            ...this.messages[index],
            textResult: data.text || '未能识别语音内容'
          };
          this.showStatus('语音转文字成功', 'success');
        } else {
          throw new Error(data.message || '语音转文字失败');
        }
      } catch (error) {
        console.error('语音转文字失败:', error);
        this.showStatus(`转文字失败: ${error.message}`, 'error');
      }
    },
    
    // 分析语调
    async analyzeVoiceTone(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'voice' || !msg.blob) return;
      
      this.showStatus('正在分析语调...', 'info');
      
      try {
        const formData = new FormData();
        formData.append('audio', msg.blob, 'recording.wav');
        
        const response = await fetch(`${this.API_BASE_URL}/extract_tone`, {
          method: 'POST',
          headers: this.commonHeaders,
          body: formData
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          console.error('语调分析API错误:', errorText);
          throw new Error(`服务器错误: ${response.status} - ${errorText.substring(0, 100)}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success') {
          // 使用扩展运算符创建新对象，触发响应式更新
          this.messages[index] = {
            ...this.messages[index],
            toneResult: data.data
          };
          this.showStatus('语调分析成功', 'success');
        } else {
          throw new Error(data.message || '语调分析失败');
        }
      } catch (error) {
        console.error('语调分析失败:', error);
        this.showStatus(`语调分析失败: ${error.message}`, 'error');
      }
    },
    
    // 计算语速
    calculateSpeechSpeed(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'voice' || !msg.textResult) return;
      
      // 计算字数（去除空白字符）
      const text = msg.textResult.trim();
      const charCount = text.length;
      
      // 音频时长（秒）
      const duration = Math.max(msg.duration, 0.1); // 避免除以0
      
      // 计算语速（字/秒）
      const speed = (charCount / duration).toFixed(1);
      
      // 使用扩展运算符创建新对象，触发响应式更新
      this.messages[index] = {
        ...this.messages[index],
        speechSpeed: speed
      };
      this.showStatus('语速计算完成', 'success');
    },
    
    // 分析语言逻辑
    async analyzeLanguageLogic(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'voice' || !msg.textResult) return;
      
      this.showStatus('正在分析语言逻辑...', 'info');
      
      try {
        // 将识别的文字作为prompt的一部分发送给chat接口
        const prompt = `分析以下文本的语言逻辑和连贯性，并给出评分(1-10分)和简要分析: "${msg.textResult}"`;
        
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });
        
        if (!response.ok) {
          throw new Error(`请求失败: ${response.status}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success') {
          // 提取评分（如果有的话）
          let score = null;
          const scoreMatch = data.result.match(/(\d+(\.\d+)?)\s*\/\s*10/);
          if (scoreMatch && scoreMatch[1]) {
            score = parseFloat(scoreMatch[1]);
          }
          
          // 使用扩展运算符创建新对象，触发响应式更新
          this.messages[index] = {
            ...this.messages[index],
            logicResult: {
              analysis: data.result,
              score: score
            }
          };
          this.showStatus('语言逻辑分析完成', 'success');
        } else {
          throw new Error(data.result || '语言逻辑分析失败');
        }
      } catch (error) {
        console.error('语言逻辑分析失败:', error);
        this.showStatus(`语言逻辑分析失败: ${error.message}`, 'error');
      }
    },
    
    // 添加消息到界面
    addMessage(sender, content, type = 'text', isError = false) {
      this.messages.push({
        sender: sender,
        content: content,
        type: type,
        isError: isError
      });
    },
    
    // 添加表情和姿态分析消息
    addExpressionMessage(imageUrl, expressionResults, poseResult) {
      this.messages.push({
        sender: 'user',
        type: 'expression',
        imageUrl: imageUrl,
        expressionResults: expressionResults,
        poseResult: poseResult,
        comprehensiveAnalysis: null
      });
      this.scrollToBottom();
    },
    
    // 添加"正在输入"指示器
    addTypingIndicator() {
      const typingId = `typing-${Date.now()}`;
      this.typingId = typingId;
      this.isTyping = true;
      return typingId;
    },
    
    // 移除"正在输入"状态
    removeTypingIndicator(id) {
      if (this.typingId === id) {
        this.isTyping = false;
        this.typingId = "";
      }
    },
    
    // 滚动到最新消息
    scrollToBottom() {
      this.$nextTick(() => {
        const container = this.$refs.messagesContainer;
        if (container) {
          container.scrollTop = container.scrollHeight;
        }
      });
    },
    
    // 测试连接状态
    async testConnection() {
      try {
        await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'OPTIONS',
          headers: this.commonHeaders
        });
        
        this.statusText = ``;
        this.statusClass = 'text-xs text-green-600 mt-2 text-center';
      } catch (error) {
        this.statusText = `连接失败: ${error.message}`;
        this.statusClass = 'text-xs text-red-600 mt-2 text-center';
      }
    },
    
    // 初始化聊天容器高度
    initContainerHeight() {
      const navBarHeight = 46;
      const bottomBarHeight = document.getElementById('dialog_bottombar_inside')?.offsetHeight || 0;
      const container = document.getElementById('dialog_content');
      if (container) {
        container.style.paddingTop = navBarHeight + 10 + 'px';
        container.style.paddingBottom = bottomBarHeight + 10 + 'px';
        container.style.height = `calc(100vh - ${navBarHeight + bottomBarHeight + 20}px)`;
      }
    },
    
    // 格式化时长显示
    formatDuration(seconds) {
      if (seconds < 60) {
        return `${seconds}s`;
      } else {
        const mins = Math.floor(seconds / 60);
        const secs = seconds % 60;
        return `${mins}:${secs < 10 ? '0' + secs : secs}`;
      }
    },
    
    // 显示状态消息
    showStatus(text, type = 'info') {
      this.statusText = text;
      switch(type) {
        case 'success':
          this.statusClass = 'text-xs text-green-600 mt-2 text-center';
          break;
        case 'error':
          this.statusClass = 'text-xs text-red-600 mt-2 text-center';
          break;
        case 'warning':
          this.statusClass = 'text-xs text-yellow-600 mt-2 text-center';
          break;
        default:
          this.statusClass = 'text-xs text-blue-600 mt-2 text-center';
      }
      
      if (type !== 'error') {
        setTimeout(() => {
          this.statusText = '';
        }, 3000);
      }
    },
    
    // 音频格式转换相关方法
    async convertToValidWav(blob) {
      if (await this.isValidWavFile(blob)) {
        return blob;
      }
      
      try {
        const arrayBuffer = await new Response(blob).arrayBuffer();
        const audioContext = new (window.AudioContext || window.webkitAudioContext)({
          sampleRate: 16000
        });
        
        const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);
        const wavBuffer = this.convertToWav(audioBuffer);
        
        return new Blob([wavBuffer], { type: 'audio/wav' });
      } catch (error) {
        console.error('音频格式转换失败:', error);
        this.showStatus('音频格式转换失败，可能影响识别效果', 'warning');
        return blob;
      }
    },
    
    async isValidWavFile(blob) {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onloadend = (e) => {
          if (e.target.readyState === FileReader.DONE) {
            const arrayBuffer = e.target.result;
            const uint8Array = new Uint8Array(arrayBuffer);
            
            if (uint8Array.length >= 4) {
              const riff = String.fromCharCode(uint8Array[0], uint8Array[1], uint8Array[2], uint8Array[3]);
              resolve(riff === 'RIFF');
            } else {
              resolve(false);
            }
          }
        };
        reader.readAsArrayBuffer(blob.slice(0, 4));
      });
    },
    
    convertToWav(audioBuffer) {
      const numberOfChannels = audioBuffer.numberOfChannels;
      const sampleRate = audioBuffer.sampleRate;
      const format = 1;
      const bitDepth = 16;
      
      let data = new Float32Array(audioBuffer.length);
      for (let i = 0; i < audioBuffer.length; i++) {
        let sum = 0;
        for (let c = 0; c < numberOfChannels; c++) {
          sum += audioBuffer.getChannelData(c)[i];
        }
        data[i] = sum / numberOfChannels;
      }
      
      const buffer = new ArrayBuffer(44 + data.length * 2);
      const view = new DataView(buffer);
      
      this.writeString(view, 0, 'RIFF');
      view.setUint32(4, 36 + data.length * 2, true);
      this.writeString(view, 8, 'WAVE');
      this.writeString(view, 12, 'fmt ');
      view.setUint32(16, 16, true);
      view.setUint16(20, format, true);
      view.setUint16(22, 1, true);
      view.setUint32(24, sampleRate, true);
      view.setUint32(28, sampleRate * 2, true);
      view.setUint16(32, 2, true);
      view.setUint16(34, bitDepth, true);
      this.writeString(view, 36, 'data');
      view.setUint32(40, data.length * 2, true);
      
      let index = 44;
      const volume = 1;
      for (let i = 0; i < data.length; i++) {
        const sample = Math.max(-1, Math.min(1, data[i] * volume));
        view.setInt16(index, sample < 0 ? sample * 0x8000 : sample * 0x7FFF, true);
        index += 2;
      }
      
      return buffer;
    },
    
    writeString(view, offset, string) {
      for (let i = 0; i < string.length; i++) {
        view.setUint8(offset + i, string.charCodeAt(i));
      }
    },
    
    // 相机相关方法 - 集成自第一段和第二段代码
    async toggleCamera() {
      if (this.isCameraActive) {
        this.stopCamera();
        this.isCameraModalVisible = false;
      } else {
        this.isCameraModalVisible = true;
        await this.startCamera();
      }
    },
    
    async startCamera() {
      this.isLoadingCamera = true;
      this.cameraError = null;
      
      try {
        if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
          throw new Error('您的浏览器不支持相机功能，请使用最新浏览器');
        }
        
        if (window.location.protocol !== 'https:' && window.location.hostname !== 'localhost') {
          throw new Error('相机功能需要在HTTPS环境下运行');
        }
        
        // 相机配置 - 来自第一段代码
        this.videoStream = await navigator.mediaDevices.getUserMedia({
          video: { 
            width: { ideal: 1280 }, 
            height: { ideal: 720 } 
          }
        });
        
        const videoElement = this.$refs.videoElement;
        if (videoElement) {
          videoElement.srcObject = this.videoStream;
          this.isCameraActive = true;
          this.showStatus('相机已启动，可进行表情和姿态识别', 'success');
        }
      } catch (error) {
        console.error('相机启动失败:', error);
        this.cameraError = error.message;
        
        // 错误处理 - 来自第二段代码
        if (error.name === 'NotAllowedError') {
          this.cameraError = '相机权限被拒绝，请在浏览器设置中允许相机访问';
        } else if (error.name === 'NotFoundError') {
          this.cameraError = '未找到相机设备';
        } else if (error.name === 'NotReadableError') {
          this.cameraError = '相机被占用或不可用';
        } else if (error.name === 'NotSupportedError') {
          this.cameraError = '您的浏览器不支持相机功能';
        }
        
        this.showStatus(`相机错误: ${this.cameraError}`, 'error');
      } finally {
        this.isLoadingCamera = false;
      }
    },
    
    stopCamera() {
      if (this.videoStream) {
        this.videoStream.getTracks().forEach(track => track.stop());
        this.videoStream = null;
      }
      
      const videoElement = this.$refs.videoElement;
      if (videoElement) {
        videoElement.srcObject = null;
      }
      
      this.isCameraActive = false;
      this.isAnalyzingContinuously = false;
      
      if (this.continuousAnalysisTimer) {
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
      }
      
      if (this.detectionInterval) {
        clearInterval(this.detectionInterval);
        this.detectionInterval = null;
      }
      
      this.showStatus('相机已关闭', 'info');
    },
    
    // 拍照并同时分析表情和姿态
    async captureImage() {
      if (!this.isCameraActive || this.isProcessingImage) return;
      
      this.isProcessingImage = true;
      this.showStatus('正在同时分析表情和姿态...', 'info');
      
      try {
        const videoElement = this.$refs.videoElement;
        if (!videoElement) throw new Error('未找到视频元素');
        
        // 创建画布并捕获当前视频帧 - 来自第一段代码
        const canvas = document.createElement('canvas');
        canvas.width = videoElement.videoWidth;
        canvas.height = videoElement.videoHeight;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(videoElement, 0, 0, canvas.width, canvas.height);
        
        // 转换为base64格式
        const base64Image = canvas.toDataURL('image/jpeg');
        
        // 并行获取表情分析和姿态分析结果
        const [expressionResults, poseResult] = await Promise.all([
          this.analyzeExpression(base64Image),
          this.analyzePose(base64Image)
        ]);
        
        // 创建图片URL用于显示
        const imageUrl = URL.createObjectURL(await fetch(base64Image).then(res => res.blob()));
        
        // 添加到消息列表
        this.addExpressionMessage(imageUrl, expressionResults, poseResult);
        
        this.showStatus('表情和姿态分析完成', 'success');
      } catch (error) {
        console.error('图像分析失败:', error);
        this.showStatus(`分析失败: ${error.message}`, 'error');
      } finally {
        this.isProcessingImage = false;
      }
    },
    
    // 开始/停止连续分析
    startContinuousAnalysis() {
      if (this.isAnalyzingContinuously) {
        // 停止连续分析
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
        this.isAnalyzingContinuously = false;
        this.showStatus('已停止连续分析', 'info');
      } else {
        // 开始连续分析，每2秒一次 - 时间间隔来自第一段代码
        this.showStatus('开始连续分析...', 'info');
        this.isAnalyzingContinuously = true;
        
        // 立即执行一次
        this.captureImage();
        
        // 设置定时器
        this.continuousAnalysisTimer = setInterval(() => {
          if (this.isAnalyzingContinuously) {
            this.captureImage();
          }
        }, 2000);
      }
    },
    
    // 分析表情 - 来自第一段代码
    async analyzeExpression(base64Image) {
      try {
        const response = await fetch(`${this.API_BASE_URL}/predict/image`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ base64: base64Image })
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`表情分析失败: ${response.status} - ${errorText.substring(0, 100)}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success' && data.predictions) {
          return data.predictions;
        } else {
          throw new Error(data.error || '未获取到表情分析结果');
        }
      } catch (error) {
        console.error('表情分析出错:', error);
        throw error;
      }
    },
    
    // 分析姿态 - 来自第二段代码
    async analyzePose(base64Image) {
      try {
        // 捕获当前视频帧并发送到后端 - 类似第二段代码的processVideoFrame方法
        const response = await fetch(`${this.API_BASE_URL}/process_pose_frame`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ base64: base64Image })
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`姿态分析失败: ${response.status} - ${errorText.substring(0, 100)}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success' && data.result) {
          return {
            pose: data.result.pose,
            score: data.result.score,
            fps: data.result.fps || 0
          };
        } else {
          throw new Error(data.error || '未获取到姿态分析结果');
        }
      } catch (error) {
        console.error('姿态分析出错:', error);
        throw error;
      }
    },
    
    // 综合分析表情和姿态
    async analyzeComprehensive(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'expression') return;
      
      this.showStatus('正在进行综合分析...', 'info');
      
      try {
        // 构建提示信息
        let expressionsText = msg.expressionResults.map(exp => 
          `${exp.emotion} (${(exp.confidence * 100).toFixed(1)}%)`
        ).join('、');
        
        let prompt = `根据以下表情和姿态信息进行综合分析:
表情: ${expressionsText}
姿态: ${msg.poseResult.pose} (置信度: ${(msg.poseResult.score * 100).toFixed(1)}%)

请分析这个人可能的情绪状态和意图，并给出简要解读。`;
        
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });
        
        if (!response.ok) {
          throw new Error(`综合分析请求失败: ${response.status}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success') {
          // 更新消息对象
          this.messages[index] = {
            ...this.messages[index],
            comprehensiveAnalysis: data.result
          };
          this.showStatus('综合分析完成', 'success');
        } else {
          throw new Error(data.result || '综合分析失败');
        }
      } catch (error) {
        console.error('综合分析失败:', error);
        this.showStatus(`综合分析失败: ${error.message}`, 'error');
      }
    }
  }
};
</script>

