  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.recording-indicator {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 20px 30px;
  border-radius: 10px;
  text-align: center;
  z-index: 1000;
}

.recording-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #f5222d;
  margin: 0 auto 10px;
  animation: blink 1s infinite;
}

@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.status-bar {
  position: absolute;
  bottom: 120px;
  left: 0;
  width: 100%;
  text-align: center;
  transition: opacity 0.3s ease;
}

/* 相机模态框样式 */
.camera-modal {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.camera-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #eee;
}

.camera-preview {
  flex-grow: 1;
