<template>
    <!-- 登录和注册页面 -->
    <div id="login">
        <!-- 背景装饰元素 -->
        <div class="bg-decoration top-left"></div>
        <div class="bg-decoration bottom-right"></div>
        
        <div id="login_error" @click="closeError()" class="error-toast"></div>
        
        <!-- 灵犀Logo和标题 -->
        <div class="logo-container">
            <div class="logo-icon">
                <span class="icon-inner">灵</span>
            </div>
            <h1 class="logo-text">灵犀</h1>
            <p class="logo-subtitle">多模态智能面试平台</p>
        </div>
        
        <!-- 登录/注册标签页 -->
        <van-tabs 
            v-model="activeTab" 
            title-active-color="#4e5ba6" 
            class="login-form"
            @change="handleTabChange"
        >
            <van-tab title="登录">
                <van-form @submit="handleLogin" class="form-content">
                    <van-cell-group inset class="form-group">
                        <van-field 
                            v-model="loginForm.username"
                            name="username"
                            label="账号"
                            placeholder="请输入账号"
                            :rules="[{ required: true, message: '请输入账号' }]"
                            class="form-field"
                        />
                        <van-field
                            v-model="loginForm.password"
                            type="password"
                            name="password"
                            label="密码"
                            placeholder="请输入密码"
                            :rules="[{ required: true, message: '请输入密码' }]"
                            class="form-field"
                        />
                    </van-cell-group>
                    <div class="form-actions">
                        <a href="#" class="forgot-link">忘记密码?</a>
                    </div>
                    <div class="login-button-container">
                        <van-button round type="primary" native-type="submit" class="login-button">
                            登录 <van-icon name="arrow-right" size="16" />
                        </van-button>
                    </div>
                </van-form>
            </van-tab>
            
            <van-tab title="注册">
                <van-form @submit="handleRegister" class="form-content">
                    <van-cell-group inset class="form-group">
                        <van-field
                            v-model="registerForm.username"
                            name="username"
                            label="账号"
                            placeholder="请设置账号"
                            :rules="[{ required: true, message: '请输入账号' }]"
                            class="form-field"
                        />
                        <van-field
                            v-model="registerForm.password"
                            type="password"
                            name="password"
                            label="密码"
                            placeholder="请设置密码"
                            :rules="[{ required: true, message: '请输入密码' }]"
                            class="form-field"
                        />
                        <van-field
                            v-model="registerForm.email"
                            name="email"
                            label="邮箱"
                            placeholder="请输入邮箱"
                            :rules="[
                                { required: true, message: '请输入邮箱' },
                                { type: 'email', message: '请输入正确的邮箱格式' }
                            ]"
                            class="form-field"
                        />
                    </van-cell-group>
                    <div class="login-button-container">
                        <van-button round type="primary" native-type="submit" class="login-button">
                            注册 <van-icon name="user-plus" size="16" />
                        </van-button>
                    </div>
                </van-form>
            </van-tab>
        </van-tabs>
        
        <!-- 底部信息 -->
        <div class="footer-info">
            <p>灵犀 © 2025 版权所有</p>
            
        </div>
    </div>
</template>

<script>
import { Button, CellGroup, Field, Form, Icon, Tabs, Tab, Toast } from 'vant';
import axios from 'axios';

// 配置API基础地址
const API_BASE_URL = 'https://123.56.203.202';

// 创建axios实例
const api = axios.create({
    baseURL: API_BASE_URL,
    timeout: 30000,
    withCredentials: true
});

export default {
    components: {
        VanButton: Button,
        VanCellGroup: CellGroup,
        VanField: Field,
        VanForm: Form,
        VanIcon: Icon,
        VanTabs: Tabs,
        VanTab: Tab
    },
    data() {
        return {
            activeTab: 0, // 0: 登录, 1: 注册
            loginForm: {
                username: "",
                password: ""
            },
            registerForm: {
                username: "",
                password: "",
                email: ""
            }
        };
    },
    methods: {
        // 处理登录
        async handleLogin(values) {
            try {
                Toast.loading({
                    message: '登录中...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/sqlLogin', {
                    username: values.username,
                    password: values.password
                });
                
                Toast.clear();
                
                if (data.success) {
                    localStorage.setItem('currentUser', JSON.stringify({
                        username: data.username,
                        id: data.id
                    }));
                    localStorage.setItem('username', data.username);
                    
                    this.$router.push('/home/message');
                    Toast.success('登录成功');
                } else {
                    Toast.fail(data.message || '账号或密码错误');
                }
            } catch (error) {
                Toast.clear();
                console.error('登录失败', error);
                Toast.fail('登录失败失败，请，请重试');
            }
        },
        
        // 处理注册
        async handleRegister(values) {
            try {
                Toast.loading({
                    message: '注册中...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/register', {
                    username: values.username,
                    password: values.password,
                    email: values.email
                });
                
                Toast.clear();
                
                if (data.success) {
                    Toast.success('注册成功，请登录');
                    this.activeTab = 0;
                } else {
                    Toast.fail(data.message || '注册失败');
                }
            } catch (error) {
                Toast.clear();
                console.error('注册失败', error);
                Toast.fail('注册失败，请重试');
            }
        },
        
        closeError() {
            document.getElementById('login_error').innerHTML = "";
        },
        
        // 标签切换动画效果
        handleTabChange() {
            const formEl = document.querySelector('.form-content');
            formEl.classList.add('tab-transition');
            setTimeout(() => {
                formEl.classList.remove('tab-transition');
            }, 300);
        }
    }
}
</script>

