  background: #cccccc;
  cursor: not-allowed;
}

@media (max-width: 960px) {
  .content {
    flex-direction: column;
  }
  
  .video-container, .info-panel {
    min-width: 100%;
  }
  
  .video-container {
    min-height: 320px;
  }
  
  .info-panel {
    order: -1; /* 移动端信息面板放上面 */
  }
}
</style>
    